"use client";
import React, { useState, useMemo } from "react";
import { 
  LayoutGrid, 
  Download, 
  Users, 
  Search, 
  FileText, 
  ChevronRight, 
  Filter,
  ArrowDownToLine,
  MoreVertical,
  BookOpen
} from "lucide-react";

// --- DUMMY DATA ---
const CLASSES_DATA = [
  { id: 1, name: "JSS 1", arm: "A", students: 42, category: "Junior", teacher: "Mr. Okon" },
  { id: 2, name: "JSS 1", arm: "B", students: 38, category: "Junior", teacher: "Mrs. Adeyemi" },
  { id: 3, name: "SSS 1", arm: "Science", students: 35, category: "Senior", teacher: "Dr. Ibrahim" },
  { id: 4, name: "SSS 1", arm: "Art", students: 28, category: "Senior", teacher: "Ms. Linda" },
  { id: 5, name: "SSS 3", arm: "Commercial", students: 31, category: "Senior", teacher: "Mr. Eze" },
  { id: 6, name: "JSS 2", arm: "C", students: 40, category: "Junior", teacher: "Mrs. Bello" },
];

export default function ClassManagement() {
  const [searchTerm, setSearchTerm] = useState("");
  const [filter, setFilter] = useState("All");

  // Filter Logic
  const filteredClasses = useMemo(() => {
    return CLASSES_DATA.filter(item => {
      const matchesSearch = item.name.toLowerCase().includes(searchTerm.toLowerCase()) || 
                            item.arm.toLowerCase().includes(searchTerm.toLowerCase());
      const matchesFilter = filter === "All" || item.category === filter;
      return matchesSearch && matchesFilter;
    });
  }, [searchTerm, filter]);

  return (
    <div className="min-h-screen bg-[#FDFDFE] text-zinc-900 font-sans selection:bg-indigo-100 pb-20">
      
      {/* --- HEADER --- */}
      <div className="sticky top-0 z-40 bg-white/80 backdrop-blur-xl border-b border-zinc-100 px-6 md:px-10 py-5">
        <div className="max-w-7xl mx-auto flex flex-col md:flex-row md:items-center justify-between gap-4">
          <div>
            <h1 className="text-2xl font-bold text-zinc-900 tracking-tight">Academic Classes</h1>
            <p className="text-xs font-medium text-zinc-500 mt-1">Manage class structures and generate terminal results.</p>
          </div>
          
          <div className="flex items-center gap-3">
            <div className="relative">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-zinc-400" size={16} />
              <input 
                type="text" 
                placeholder="Search class or arm..."
                className="pl-10 pr-4 py-2.5 bg-zinc-50 border border-zinc-200 rounded-xl text-sm focus:outline-none focus:ring-2 focus:ring-indigo-500/20 focus:border-indigo-500 w-full md:w-64 transition-all"
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
              />
            </div>
            <button className="p-2.5 bg-white border border-zinc-200 rounded-xl hover:bg-zinc-50 text-zinc-600 transition-all">
              <Filter size={18} />
            </button>
          </div>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-6 md:px-10 mt-10">
        
        {/* --- STATS ROW --- */}
        <div className="grid grid-cols-1 sm:grid-cols-3 gap-6 mb-10">
          <StatCard icon={<LayoutGrid size={20}/>} label="Total Classes" value={CLASSES_DATA.length} color="indigo" />
          <StatCard icon={<Users size={20}/>} label="Total Students" value="214" color="blue" />
          <StatCard icon={<FileText size={20}/>} label="Pending Results" value="4" color="orange" />
        </div>

        {/* --- FILTER TABS --- */}
        <div className="flex items-center gap-2 mb-8 overflow-x-auto pb-2">
          {["All", "Junior", "Senior"].map((type) => (
            <button
              key={type}
              onClick={() => setFilter(type)}
              className={`px-5 py-2 rounded-full text-xs font-bold uppercase tracking-wider transition-all border ${
                filter === type 
                ? "bg-zinc-900 border-zinc-900 text-white shadow-lg" 
                : "bg-white border-zinc-200 text-zinc-500 hover:border-zinc-400"
              }`}
            >
              {type} Sectors
            </button>
          ))}
        </div>

        {/* --- CLASS GRID --- */}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredClasses.map((cls) => (
            <ClassCard key={cls.id} cls={cls} />
          ))}
          
          {/* Add New Class Placeholder */}
          <button className="border-2 border-dashed border-zinc-200 rounded-3xl p-8 flex flex-col items-center justify-center text-zinc-400 hover:border-indigo-300 hover:bg-indigo-50/30 transition-all group">
             <div className="w-12 h-12 rounded-full bg-zinc-50 flex items-center justify-center mb-3 group-hover:scale-110 transition-transform">
                <LayoutGrid size={20} />
             </div>
             <span className="text-sm font-bold tracking-tight text-zinc-500">Create New Class</span>
          </button>
        </div>
      </div>
    </div>
  );
}

// --- SUB-COMPONENTS ---

function StatCard({ icon, label, value, color }: any) {
  const colors: any = {
    indigo: "bg-indigo-50 text-indigo-600",
    blue: "bg-blue-50 text-blue-600",
    orange: "bg-orange-50 text-orange-600",
  };
  return (
    <div className="bg-white p-6 rounded-3xl border border-zinc-100 shadow-sm flex items-center gap-5">
      <div className={`w-12 h-12 rounded-2xl flex items-center justify-center ${colors[color]}`}>
        {icon}
      </div>
      <div>
        <p className="text-[11px] font-bold text-zinc-400 uppercase tracking-widest">{label}</p>
        <p className="text-2xl font-black text-zinc-900">{value}</p>
      </div>
    </div>
  );
}

function ClassCard({ cls }: { cls: any }) {
  return (
    <div className="bg-white rounded-3xl border border-zinc-100 p-6 hover:shadow-xl hover:shadow-indigo-500/5 transition-all group relative overflow-hidden">
      {/* Decorative background element */}
      <div className="absolute -right-4 -top-4 w-24 h-24 bg-zinc-50 rounded-full opacity-50 group-hover:scale-150 transition-transform duration-700" />
      
      <div className="relative z-10">
        <div className="flex items-start justify-between mb-6">
          <div>
            <span className={`text-[10px] font-bold px-2.5 py-1 rounded-lg uppercase tracking-wider mb-2 inline-block ${
              cls.category === 'Junior' ? 'bg-blue-50 text-blue-600' : 'bg-purple-50 text-purple-600'
            }`}>
              {cls.category} School
            </span>
            <h3 className="text-xl font-black text-zinc-900">{cls.name} <span className="text-indigo-600">{cls.arm}</span></h3>
          </div>
          <button className="text-zinc-300 hover:text-zinc-600 transition-colors">
            <MoreVertical size={20} />
          </button>
        </div>

        <div className="space-y-4 mb-8">
           <div className="flex items-center gap-3 text-sm text-zinc-500 font-medium">
              <Users size={16} className="text-zinc-400" />
              <span>{cls.students} Enrolled Students</span>
           </div>
           <div className="flex items-center gap-3 text-sm text-zinc-500 font-medium">
              <BookOpen size={16} className="text-zinc-400" />
              <span>Form Teacher: <strong>{cls.teacher}</strong></span>
           </div>
        </div>

        <div className="grid grid-cols-2 gap-3">
           <button className="flex items-center justify-center gap-2 py-3 px-4 bg-zinc-50 text-zinc-600 rounded-2xl text-xs font-bold hover:bg-zinc-100 transition-all">
              View List <ChevronRight size={14} />
           </button>
           <button className="flex items-center justify-center gap-2 py-3 px-4 bg-indigo-600 text-white rounded-2xl text-xs font-bold hover:bg-indigo-700 shadow-lg shadow-indigo-100 transition-all">
              <Download size={14} /> Results
           </button>
        </div>
      </div>
    </div>
  );
}